import { Injectable } from "@angular/core";
import { HttpClient, HttpResponse, HttpHeaders } from "@angular/common/http";
import { Observable, Subject, BehaviorSubject } from "rxjs";
import { User } from "../_models";
import { SessionStorageService, LocalStorageService } from "ngx-webstorage";

@Injectable({ providedIn: "root" })
export class AccountService {
  private userIdentity: User;
  private authenticated = false;
  private authenticationState = new Subject<any>();
  private _credentials: any;
  constructor(private http: HttpClient) {}

  get credentials(): any {
    return this._credentials;
  }
  set credentials(credentials: any) {
    this._credentials = credentials;
  }

  fetchX(): Observable<HttpResponse<User>> {
    const headers = new HttpHeaders({});

    return this.http.get<User>("api/user/authenticate", {
      headers: headers,
      observe: "response"
    });
  }
  authenticateUser(): Observable<HttpResponse<any>> {
    const headers = new HttpHeaders({});

    return this.http.post("api/user/authenticate", this._credentials, {
      observe: "response"
    });
  }
  save(account: any): Observable<HttpResponse<any>> {
    return this.http.post("api/auth/account/", account, {
      observe: "response"
    });
  }

  authenticate(identity) {
    this.userIdentity = identity;
    this.authenticated = identity !== null;
    this.authenticationState.next(this.userIdentity);
  }

  hasAnyAuthority(authorities: string[]): boolean {
    if (
      !this.authenticated ||
      !this.userIdentity ||
      !this.userIdentity.authorities
    ) {
      return false;
    }
    if (!authorities) {
      return true;
    }
    for (let i = 0; i < authorities.length; i++) {
      if (
        this.userIdentity.authorities.filter(
          at => at.authority === authorities[i]
        ).length > 0
      ) {
        return true;
      }
    }

    return false;
  }

  hasAuthority(authority: string): Promise<boolean> {
    if (!this.authenticated) {
      return Promise.resolve(false);
    }

    return this.identity().then(
      id => {
        return Promise.resolve(
          id.authorities && id.authorities.includes(authority)
        );
      },
      () => {
        return Promise.resolve(false);
      }
    );
  }

  identity(force?: boolean): Promise<any> {
    if (force) {
      this.userIdentity = undefined;
    }

    // check and see if we have retrieved the userIdentity data from the server.
    // if we have, reuse it by immediately resolving
    if (this.userIdentity) {
      return Promise.resolve(this.userIdentity);
    }

    // retrieve the userIdentity data from the server, update the identity object, and then resolve.
    return this.authenticateUser()
      .toPromise()
      .then(response => {
        const account = response.body as User;
        if (account) {
          this.userIdentity = account;
          this.authenticated = true;
        } else {
          this.userIdentity = null;
          this.authenticated = false;
        }
        this.authenticationState.next(this.userIdentity);
        return this.userIdentity;
      })
      .catch(err => {
        this.userIdentity = null;
        this.authenticated = false;
        this.authenticationState.next(this.userIdentity);
        return null;
      });
  }

  isAuthenticated(): boolean {
    return this.authenticated;
  }

  isIdentityResolved(): boolean {
    return this.userIdentity !== undefined;
  }

  getAuthenticationState(): Observable<any> {
    return this.authenticationState.asObservable();
  }

  getImageUrl(): string {
    return this.isIdentityResolved() ? this.userIdentity.imageUrl : null;
  }
  getUser(): User {
    return this.userIdentity;
  }

  logout() {
    this._credentials = { username: "", password: "" };
    this.authenticated = false;
    this.userIdentity = null;
    this.authenticationState.next(this.userIdentity);
  }
}
