import { Component } from "@angular/core";
import { AccountService, StateStorageService } from "../../core/";
import { Router } from "@angular/router";
import { FormControl, FormGroup } from "@angular/forms";

@Component({
  selector: "app-login",
  templateUrl: "login.component.html"
})
export class LoginComponent {
  credentialsForm: FormGroup;
  credentials = { username: "", password: "" };
  constructor(
    private app: AccountService,
    private router: Router,
    private stateStorageService: StateStorageService
  ) {
    this.credentialsForm = new FormGroup({
      username: new FormControl(""),
      password: new FormControl("")
    });
    this.logout();
  }
  login() {
    this.credentials.username = this.credentialsForm.value.username;
    this.credentials.password = this.credentialsForm.value.password;
    this.app.credentials = this.credentials;
    this.app
      .identity(true)
      .then(response => {
        console.log("response:" + response);
        if (
          this.router.url === "/register" ||
          /^\/activate\//.test(this.router.url) ||
          /^\/reset\//.test(this.router.url)
        ) {
          this.router.navigate([""]);
        }

        // previousState was set in the authExpiredInterceptor before being redirected to login modal.
        // since login is successful, go to stored previousState and clear previousState
        const redirect = this.stateStorageService.getUrl();
        if (redirect) {
          this.stateStorageService.storeUrl(null);
          this.router.navigate([redirect]);
        } else {
          this.router.navigate(["/"]);
        }
      })
      .catch(err => {
        console.log("error:" + err);
      });
  }
  keyDownFunction(event) {
    if (event.keyCode === 13) {
      this.login();
    }
  }
  logout() {
    this.app.logout();
  }
}
