import { Component, OnDestroy, Inject, ChangeDetectorRef } from "@angular/core";
import { DOCUMENT } from "@angular/common";
import { navItems } from "../../_nav";
import { AccountService } from "../../core";
import { Router } from "@angular/router";

@Component({
  selector: "app-dashboard",
  templateUrl: "./default-layout.component.html"
})
export class DefaultLayoutComponent implements OnDestroy {
  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;
  constructor(
    private ref: ChangeDetectorRef,
    private appService: AccountService,
    private router: Router,
    @Inject(DOCUMENT) _document?: any
  ) {
    this.changes = new MutationObserver(mutations => {
      this.sidebarMinimized = _document.body.classList.contains(
        "sidebar-minimized"
      );
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ["class"]
    });

    this.appService.getAuthenticationState().subscribe(state => {
      this.changes.disconnect();
      if (state === undefined || state === null) {
        this.router.navigate(["/login"], { queryParams: { returnUrl: "/" } });
      }
    });
  }

  ngOnDestroy(): void {
    this.changes.disconnect();
  }
  logout() {
    this.appService.logout();
  }
}
