import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { LocalStorageService, SessionStorageService } from "ngx-webstorage";
import {
  HttpInterceptor,
  HttpRequest,
  HttpHandler,
  HttpEvent
} from "@angular/common/http";
import { AccountService } from "./account.service";

@Injectable({
  providedIn: "root"
})
export class AuthInterceptorService implements HttpInterceptor {
  constructor(
    private localStorage: LocalStorageService,
    private sessionStorage: SessionStorageService,
    private accountService: AccountService
  ) {}

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    if (!request || !request.url) {
      return next.handle(request);
    }
    const token =
      this.localStorage.retrieve("authenticationToken") ||
      this.sessionStorage.retrieve("authenticationToken");
    if (!!this.accountService.credentials) {
      request = request.clone({
        setHeaders: {
          AuthorizationTest: "Basic " + btoa(this.accountService.credentials.username + ":" + this.accountService.credentials.password),
          "Content-Type": "application/json",
          "X-Auth-Username": this.accountService.credentials.username,
          "X-Auth-Password": this.accountService.credentials.password
        }
      });
    } else {
      request = request.clone({
        setHeaders: {
          "Content-Type": "application/json"
        }
      });
    }
    return next.handle(request);
  }
}
